import {Registry} from 'cs-web-components-base';
import {prefixNS} from './helpers';
import Briefcase from './Briefcase';
import Form from './Form';
import WFTaskDetails from './WFTaskDetails';

Registry.registerComponent(prefixNS('Briefcase'), Briefcase);
Registry.registerComponent(prefixNS('Form'), Form);
Registry.registerComponent(prefixNS('WFTaskDetails'), WFTaskDetails);

module.exports = [Briefcase, Form, WFTaskDetails];  // eslint-disable-line no-undef



